// chat window scss
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/layout.scss';

.web-chat-window {
    &__chat-window-container {
        height: 406px;

        .messages-container {
            @include eureka-scroll-y;
            height: 300px;
        }
    }
}
.web-chat-window-container__tile.web-chat-window__chat-window-container {
    top: 0;
}
