// chat window scss
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/layout.scss';

.web-chat-image-bubble {
    margin: 8px 5px;
    $this: &;

    &__bubble-loading-container {
        max-width: var(--chat-bubble-max-width);
        border-radius: var(--eureka-chat-bubble-border-radius);
        padding: 0.8rem 1.2rem;
        text-align: center;
        position: relative;

        .eureka-loading-dots__dot {
            width: 0.15em;
            height: 0.15em;
        }

        #{$this}__filename {
            font-size: var(--eureka-core-typography-footnote-font-size);
            padding-top: 0.3rem;
            padding-bottom: 0.3rem;
        }

        #{$this}__tail {
            position: absolute;
            display: block;
            width: 12px;
            height: 15px;
            bottom: 0;
        }
    }

    &__bubble-thumbnail-container {
        max-width: var(--chat-bubble-max-width);
        border-radius: var(--eureka-chat-bubble-border-radius);
        -webkit-border-radius: var(--eureka-chat-bubble-border-radius);
        -moz-border-radius: var(--eureka-chat-bubble-border-radius);
        max-width: 100px;
        max-height: 133px;
        cursor: pointer;
        position: relative;
        overflow: hidden;

        #{$this}____thumbnail {
            border-radius: var(--eureka-chat-bubble-border-radius);
            -webkit-border-radius: var(--eureka-chat-bubble-border-radius);
            -moz-border-radius: var(--eureka-chat-bubble-border-radius);
            -khtml-border-radius: var(--eureka-chat-bubble-border-radius);
        }

        #{$this}__modal-scrim {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 100%;
            height: 100%;
            min-height: 6rem;
            border-radius: var(--eureka-chat-bubble-border-radius);
            position: absolute;
            top: 0;
            color: transparent;
            text-align: center;
        }

        #{$this}__modal-scrim:hover {
            color: var(--eureka-core-color-canvas);
            background-color: var(--eureka-core-color-universal-focus-halo);
        }
    }

    &__bubble-thumbnail-container:focus-visible {
        outline-color: var(--eureka-tabs-active-focus-halo-color);
        outline-width: 0.2rem;
        outline-style: solid;
        border-radius: var(--eureka-chat-bubble-border-radius);
        -webkit-border-radius: var(--eureka-chat-bubble-border-radius);
        -moz-border-radius: var(--eureka-chat-bubble-border-radius);
        -khtml-border-radius: var(--eureka-chat-bubble-border-radius);

        #{$this}__modal-scrim {
            color: var(--eureka-core-color-canvas);
            background-color: var(--eureka-core-color-universal-focus-halo);
        }
    }

    &__thumbnail-container {
        @include eureka-flex-column;
        min-height: 60px;
        justify-content: center;
        border-radius: var(--eureka-chat-bubble-border-radius);
    }

    &--from-me {
        @include eureka-flex-row-reverse;

        #{$this}__bubble-loading-container {
            background-color: var(--eureka-core-color-primary);
            color: var(--eureka-core-color-canvas);
        }

        .eureka-loading-dots__dot {
            background-color: var(--eureka-core-color-canvas);
        }

        #{$this}__tail {
            right: -6px;
            color: var(--eureka-core-color-primary);
        }
    }

    &--from-other {
        @include eureka-flex-row;

        #{$this}__bubble-loading-container {
            background-color: var(--eureka-core-color-canvas-alt);
            color: var(--eureka-core-color-text);
        }

        .eureka-loading-dots__dot {
            background-color: var(--eureka-progress-tracker-step-color-text);
        }

        #{$this}__tail {
            left: -5px;
            color: var(--eureka-core-color-canvas-alt);
        }
    }
}
