// ========================================================
// Dependencies
// ========================================================
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/utils/map-utils.scss';
@import '../styles/tokens/style-tokens.scss';

// ========================================================
// Paths
// ========================================================
$eureka-averta-font-path: '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/media/fonts/averta';
$eureak-averta-font-fam: 'Averta';

@font-face {
    font-family: $eureak-averta-font-fam;
    src: url('#{$eureka-averta-font-path}/Averta-Regular.otf') format('truetype');
    font-weight: normal;
    font-style: normal;
    font-display: fallback;
}

@font-face {
    font-family: $eureak-averta-font-fam;
    src: url('#{$eureka-averta-font-path}/Averta-RegularItalic.otf') format('truetype');
    font-weight: normal;
    font-style: italic;
    font-display: fallback;
}

@font-face {
    font-family: $eureak-averta-font-fam;
    src: url('#{$eureka-averta-font-path}/Averta-Semibold.otf') format('truetype');
    font-weight: bold;
    font-style: normal;
    font-display: fallback;
}

@font-face {
    font-family: $eureak-averta-font-fam;
    src: url('#{$eureka-averta-font-path}/Averta-SemiboldItalic.otf') format('truetype');
    font-weight: bold;
    font-style: italic;
    font-display: fallback;
}
